button{
    margin: 0;
    border: 0;
    outline: 0;
    cursor: pointer;
    padding:0;
    background-color: white;
}
%btn{
    padding: 10px 20px;
    font-size: 15px;
    transition: background-color 0.3s;
}
.btn_primary{
    @extend %btn;
    color: white;
    border-radius: 50px;
    background-color: $primary;
}
.btn_secondary{
    @extend %btn;
    border: solid 2px #DADBEE;
    border-radius: 4px;
    &:hover{
        background-color: #DADBEE;
    }
}
.disabled{
    opacity: 0.5;
}