.countDown{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    .countDown__time{
        font-size: 150px;
        max-width: 400px;
        margin-right: 45px;
        text-align: center;
        font-weight: bold;
        border:0;
        outline: 0;
        &:focus{
            border:0;
            outline: 0;
        }
    }
}
.controls{
    margin-top: 20px;
    display: flex;
    align-items: center;
    button{
        cursor: pointer;
        &:nth-child(2){
            margin:0 100px;
        }
        &:nth-child(1),&:nth-child(2){
            opacity: 0.5;
            transition: opacity 0.3s;
            &:hover{
                opacity: 1;
            }
        }
    }
}

