header{
    display: flex;
    justify-content: space-between;
    height: 100px;
    h2{
        color: $primary;
        font-weight: bold;
        margin-left : 25px;
        align-self: center;
    }
    button{
        font-weight: bold;
        margin-right : 25px;
        align-self: center;
        &:hover{
            box-shadow: 0 0 8px 3px $primaryLight;
        }
    }
    box-shadow: 0px 0px 18px 5px rgba(128, 128, 128,0.3);
}
nav{
    display: flex;
    margin-top: 15px;
    div{
        cursor: pointer;
        width: 100px;
        padding-top: 10px;
        text-align: center;
        border-bottom: 3px solid transparent;
        transition: box-shadow 0.3s,border-bottom-color 0.3s;
        &:nth-child(2){
            margin: 0 40px;
        }
        &:hover,&.active{
            border-bottom-color:black;
            box-shadow: 0 0 8px rgba(128, 128, 128,0.6);
        }
    }
}